import React, { Component } from 'react'
import { Pane, Heading, Button, AnnotationIcon, Paragraph } from 'evergreen-ui'
import TableCmp from '../../@components/TableCmp';
import ConfigService from '../../@services/Config';
import Busy from '../../@components/Busy';

class PageTemplatesInstalled extends Component {

  constructor() {
    super()
    this.state = {
      tableCols: [
        {label:'Type', key: 'type'},
        {label:'Description', key: 'description'}
      ],
      tableData: [],
      ready: false
    }
    this.mounted = false
  }
  
  componentDidMount()
  {
    
    this.mounted = true
    
    this.updateTableData();

  }

  componentWillUnmount()
  {
    
    this.mounted = false

  }

  async updateTableData() {
    
    if (!this.mounted) return;
    
    ConfigService.getList('templates') // TODO: change to root paths
      .then((values) => {

        //
        this.setState({
          tableData: values.map(item => {
            return { value: item.value, type: item.type, description: item.description }
          }),
          ready: true
        })

      })
    
  }

  render() {
    return (
      <article>
        <Pane display="flex" padding=".5em" background="tint2">
          <Pane flex={1} alignItems="center" display="flex">
            <Heading size={600}>Installed</Heading>
          </Pane>
          <Pane>
            <Button appearance="minimal" iconBefore={AnnotationIcon}>Add Template</Button>
          </Pane>
        </Pane>
        <Pane padding="1em">
          <Heading size={400}>What is this?</Heading>
          <Paragraph size={400} color="muted">
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam laoreet non augue et faucibus. Ut arcu tellus, hendrerit id sem in, iaculis varius ipsum. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos.
          </Paragraph>
        </Pane>
        <Pane>
          {
            (this.state.ready) ?
            (<TableCmp emptyMsg={`Nothing to display`} cols={this.state.tableCols} rows={this.state.tableData} />) :
            <Busy/>
          }
        </Pane>
      </article>
    )
  }
}

export default PageTemplatesInstalled;